.menu_about{
    padding-top: 270px;
    max-width: 1366px;
    margin: auto;
    background-color: rgba(0,0,0,0.5);   
    
   
}


.click{
    text-align: center;
    padding: 20px;
    margin-top: 80px;
    font-size: 25px;
}

.menu_about_info{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 20px;
   
    font-size: 20px;
    border-radius: 10px;
    /* animation-name: animate6;
      animation-duration: 3s;
      animation-iteration-count: 1;
      animation-fill-mode: forwards;
      animation-delay: 2s; */
      animation: animate2 2s forwards .1s ease-in-out; 
      opacity: 0;
      background-color: rgba(0,0,0,0.3);
      box-shadow: 0 0 0.7142857142857143rem #cccccc;
     
  }
  @keyframes animate2{
        
      from{
      transform: translateX(100px);
      }
      
      to{
          opacity: 1;
          transform: translateX(0px);
          
        }
      }

.menu_about_info img{
    border-radius: 50%;
    object-fit: cover;
    width: 125px;
    
    box-shadow: 0 0 0.7142857142857143rem #cccccc;
    
    
    
}

.menu_about_skills ul{
    display: flex;
   justify-content: center;
   list-style: none;
}
.menu_about_skills a {
   text-decoration: none;
   padding: 20px;
}

.menu_about_skills_content{
   
    display: flex;
    justify-content: space-between;
    border-radius: 10px;
    margin-top: 80px;
    background-color: rgba(0,0,0,0.3);
    box-shadow: 0 0 0.7142857142857143rem #cccccc;
}

.menu_programming{
    display: flex;
    width: 100%;
    padding: 20px;
    line-height: 2.5em;
    font-size: 20px;

    
}
.menu_about_skills{
    padding: 20px;
    border-radius: 5px;
    font-size: 20px;
    box-shadow: 0 0 0.7142857142857143rem #cccccc;
        /* animation-name: animate6;
      animation-duration: 3s;
      animation-iteration-count: 1;
      animation-fill-mode: forwards;
      animation-delay: 2s; */
      animation: animate3 2s forwards .1s ease-in-out; 
      opacity: 0;
      background-color: rgba(0,0,0,0.3);
      width: 350px;
      margin: auto;
      margin-top: 10px;
     
     
  }
  @keyframes animate3{
        
      from{
      transform: translateY(100px);
      }
      
      to{
          opacity: 1;
          transform: translateY(0px);
          
        }
      }
.menu_programming_left{
    width: 30%;
    border-right: 1px dotted #ddd;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    text-align: center;
}
.menu_programming_right{
    width: 70%;
    padding: 0px 20px;
   
    
}

.menu_programming ul, a{
    list-style: none;
    text-decoration: none;
}

.menu_about_flex{
    display: flex;
}

@media(max-width:768px){
    .menu_programming{
        flex-direction: column;
        width: 100%;
    }
    .menu_programming_left{
       border: none;
        width: 100%;
        margin-bottom: 80px;
    }
    .menu_programming_right{
        margin-top: -30px;
        width: 100%;
    }
    .menu_about_info{
        justify-content: center;
        flex-direction: column;
        width: 100%;
        margin: auto;
      
        
    }
    .menu_about_skills_content{
        margin-top: 20px;
        width: 100%;
    }
}