.wrap{   
      position: absolute;
        
    }
 
    
    
    
    .widget p{
        display: inline-block;
        line-height: 1em;
    }
    
    .fecha{
        font-family: arial;
        text-align: center;
        /* font-size: 1.5em; */
        font-size: 1em;
        margin-bottom: 0.3125rem;
        /* margin-bottom: 5px; */
        background: rgba(0,0,0,.5);
        /* background-color: royalblue; */
        padding: 20px;
        width: 100%;
        
    }
    
    .reloj{
        font-family: arial;
        width: 100%;
        padding: 0px;
        /* font-size: 4em; */
        font-size: 3em;
        text-align: center;
        background: rgba(0,0,0,.5);
        /* background-color: saddlebrown; */
        display: flex;
        justify-content: center;
        align-items: center;
      
    }
    
    .caja-segundos{
       display: flex;
       flex-direction: column;
       align-items: center;
       justify-content: space-between;
    }   
    
    .segundos, .ampm { 
       font-size: 1.3rem;    
    }
    
    @media(max-width:768px){
        .wrap{
            right: 0;
            margin-top: 110px;
            background-color: rgba(0,0,0,.5);
        }
        
    }
    @media(min-width:768px){
        .wrap{
            padding-right:20px ;
            right: 0;
            margin-top: 110px;
            background-color: rgba(0,0,0,.5);
            
        }
        
    }