.home_welcome{
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    background-color: rgba(0,0,0,0.5);
    height: 100vh;
    font-size: 35px;
    
 
}
.home_welcome h1{
        /* animation-name: animate6;
      animation-duration: 3s;
      animation-iteration-count: 1;
      animation-fill-mode: forwards;
      animation-delay: 2s; */
      animation: animate6 3s forwards 0.1s ease-in-out; 
      opacity: 0;
     
  }
  @keyframes animate6{
        
      from{
      transform: translateY(100px);
      }
      
      to{
          opacity: 1;
          transform: translateY(0px);
          
        }
      }

      .home_welcome p{
    /* animation-name: animate6;
      animation-duration: 3s;
      animation-iteration-count: 1;
      animation-fill-mode: forwards;
      animation-delay: 2s; */
      animation: animate5 3s forwards 2s ease-in-out; 
      opacity: 0;
     
  }
  @keyframes animate5{
        
      from{
      transform: translateX(100px);
      }
      
      to{
          opacity: 1;
          transform: translateX(0px);
          
        }
      }
      


.boton_home{
    box-shadow: 0 0 0.7142857142857143rem #cccccc;
    padding: 10px ;
    border-radius: 50px;
    width: 200px;
    cursor: pointer;
        /* animation-name: animate6;
      animation-duration: 3s;
      animation-iteration-count: 1;
      animation-fill-mode: forwards;
      animation-delay: 2s; */
      animation: animate4 2s forwards 3s ease-in-out; 
      opacity: 0;
     
  }
  @keyframes animate4{
        
      from{
      transform: translateY(100px);
      }
      
      to{
          opacity: 1;
          transform: translateY(0px);
          
        }
      }
.boton_home:hover{
    box-shadow: 0 0 0.7142857142857143rem #eeeeee;
    color: lawngreen;
   
    
}

.welcome_text{
    color: lawngreen;
}



@media(max-width:768px){
    .home_welcome{
     font-size: 23px;
        
     
    } 
    .home_welcome p{
        display: none;
        
    } 

    .boton_home{
        margin-top: -20px;
    }
}